* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.heading {
  text-align: center;
  color: darkblue;
}

li {
  list-style: none;
}

.container {

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
}

.country-flag {
  width: 20px;
  margin-right: 10px;
}

.country-flag--big {
  width: 50px;
  margin-right: 20px;
}

body {
  background-color: lightgray;
}
